.header {
  background-color: lightblue;
  border: 1px solid black;
  margin-bottom: 10px;
  height: 40px;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.header:hover {
  cursor: pointer;
}

.header .sort {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  height: 40px;
}

.header .sort img {
  height: 30px;
  margin-left: 10px;
}

.header .sort .sort_title {
  margin-left: 50px;
}


