.item {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-align: center;
      align-items: center;
  position: relative;
  margin-bottom: 10px;
  border: 1px solid black;
  border-radius: 3px;
}

.item:hover {
  background-color: lightgrey;
}

.item a {
  line-height: 40px;
  text-decoration: none;
  width: 100%;
  margin-left: 10px;
}

.item .date {
  position: absolute;
  top: 0;
  right: 5px;
  font-size: 12px;
}

.item .logo {
  height: 40px;
  width: 40px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.item .logo img {
  height: 35px;
  border-radius: 100%;
}


